* {
  box-sizing: border-box;
}

.App {
  text-align: center;
  color: #2f2f2f;
  background-color: #e7efec;
  font-family: "Open Sans", sans-serif;
}

.table {
  width: 1120px;
  border-collapse: collapse;
  max-width: 1140px;
  margin: 0 auto;
  padding: 40px 0 20px;
  text-align: left;
  background-color: #fff;
}

.thead th {
  font-size: 16px;
  color: #95989a;
  background-color: #fff;
  padding: 12px;
  line-height: 40px;
}

.flight-item td {
  font-size: 18px;
  padding: 16px 10px;
  line-height: 40px;
  box-sizing: border-box;
}

.flight-item:first-child {
  padding-left: 30px;
}

.term-a,
.term-d {
  color: #63c745;
  font-weight: 700;
  border: 1px solid #63c745;
  border-radius: 50%;
  display: inline-block;
  width: 40px;
  height: 40px;
  text-align: center;
  line-height: 35px;
  font-size: 22px;
}

.term-d {
  color: #1eb7ee;
  border: 1px solid #1eb7ee;
}

tbody tr:nth-of-type(odd) {
  background-color: rgba(0,102,135,0.1);
}

.buttons {
  margin: 0 auto;
  display: flex;
  color: #fff;
  width: 1121px;
}

tbody tr {
  transition: all 0.3s;
}

tbody tr:hover {
  box-shadow: 0 5px 10px rgba(0,0,0,0.2);
}

tr:hover a {
  opacity: 1;
  transition: all 0.3s;
  text-decoration: underline;
  cursor: pointer;
}

a {
  color: #1eb7ee;
  opacity: 0;
}

button {
  padding: 0;
  font-size: 24px;
  line-height: 60px;
  width: 100%;
  border: none;
  outline: none;
  border-radius: 60px 60px 0 0;
  transition: all 0.3s;
  background-color: #73d2f0;
  color: #fff;
}

button:hover {
  background-color: #1eb7ee;
  cursor: pointer;
}

button:first-child {
  margin-right: -100px;
}

button:last-child {
  margin-left: -100px;
}

button.active {
  color: #1eb7ee;
  background-color: #fff;
  z-index: 1;
}

button.no-active {
  background-color: #73d2f0;
}

.departure {
  width: 610px;
}

.arrival {
  width: 610px;
}
